const form = document.querySelector('#contact-form');

form.addEventListener('submit', (e) => {
    e.preventDefault();
    const data = new FormData(e.target);
    console.log(data);
    const resultHTML = document.querySelector('.result');
    resultHTML.innerHTML = `<p>Processing...</p>`
    fetch(e.target.action, {
        method: form.method,
        body: data,
        headers: {
            'Accept': 'application/json'
        }
    }).then(() => {
        resultHTML.innerHTML = `<p>Message Sent!</p>`;
    }).catch((err) => {
        console.error(new Error(err));
        resultHTML.innerHTML = `<p>Error sending message, check console for more details</p>`;
    })
})